<script>
import Modal from "./ModalRegisterDocente";
import TitleCard from './Cards/TitleCard';
import { mapState, mapActions } from "vuex";
export default {
  components: {
    Modal,
    TitleCard
  },
  data: () => ({
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    curso: '',
    items: [
      'Matemática',
      'Física',
      'Comunicación'
    ],
    search: ''
  }),
  computed: {
    ...mapState(['docentes'])
  },
  mounted(){
    this.$store.dispatch('getDocentes');
    this.$store.dispatch('updateGlobalIndex');
  }
};
</script>

